<template>
    <div id="loading" v-if="flag">
        <div class="dot"></div>
        <div class="dot"></div>
        <div class="dot"></div>
        <div class="dot"></div>
        <div class="dot"></div>
    </div>
</template>
<script>
export default {
  name: "Loading",
  props: {
      flag: Boolean,
  }
};
</script>
<style lang="scss">
@import "../assets/scss/config.scss";
#loading {
  position: fixed;
  top: 100px;
  left: 700px;
  z-index: 999;
  $colors: #31c27c, #31c27c, #31c27c, #31c27c, #31c27c;
  display: flex;
  .dot {
    position: relative;
    width: 16px;
    height: 16px;
    margin: 0.8em;
    border-radius: 50%;
    &::before {
      position: absolute;
      content: "";
      width: 100%;
      height: 100%;
      background: inherit;
      border-radius: inherit;
      animation: wave 2s ease-out infinite;
    }

    @for $i from 1 through 5 {
      &:nth-child(#{$i}) {
        background: nth($colors, $i);

        &::before {
          animation-delay: $i * 0.2s;
        }
      }
    }
  }
}

@keyframes wave {
  50%,
  75% {
    transform: scale(2.5);
  }

  80%,
  100% {
    opacity: 0;
  }
}
</style>
