<template>
  <div id="header" :class="[flag?'sticky': '']">
    <div class="music-container">
      <div class="login" @click="login" v-if="!loginFlag">登录</div>
      <div class="user" v-if="loginFlag">
        <img v-lazy="user.avatarUrl" alt="">
        <span class="nick-name">{{user.nickname}}</span>
        <div class="detail">
            <div class="detail-container">
              <span v-for="(item, index) in user.user_PlayList" :key="index" @click="getPlayDetail(item.id)">{{item.name}}</span>
            </div>
        </div>
      </div>
      <ul class="header-container">
        <li class="item" @click="goIndex()">首页</li>
        <li class="item" @click="goSinger()">歌手</li>
        <li class="item" @click="goRank()">排行版</li>
        <li class="item" @click="goTV()">电台</li>
        <li class="item" @click="goMv()">MV</li>
      </ul>
    </div>
    <el-dialog title="网易云音乐APP扫码" :visible.sync="dialogTableVisible">
         <img v-lazy="qrUrl"/>
    </el-dialog>
    </div>
</template>
<script>
import { req_login_key, req_login_qr, req_login_state, req_user_detail, req_user_playList} from '../network/login/index.js';
export default {
  name: "NavHeader",
  props: {
    flag: Boolean,
    userFlag: Number,
  },
  data() {
   return {
     unikey: '',
     qrUrl: '',
     id: 0,
     dialogTableVisible: false,
     cookie: "",
     loginFlag: false,
     user: {
       avatarUrl: "",
       nickname: "",
       userId: 0,
       user_PlayList: [],
     }
   }
  },
  watch: {
    cookie: function() {
       // 获取用户信息
      this.getUserDetail();
    },
    dialogTableVisible: function() {
      if(!this.dialogTableVisible) {
        clearInterval(this.id);
      }
    },
    userFlag: {
      handler() {
         this.init();
      },
      immediate: true,
    }
  },
  methods: {
    init() {
      // 转成布尔值
      let flag = !! window.localStorage.getItem("loginFlag");
      if(flag) {
         this.loginFlag = true;
         this.user = JSON.parse(window.localStorage.getItem("user"));
      }else {
        this.loginFlag = false;
      }
    },
    goIndex() {
      this.$router.push({
        path: `/index`,
      })
    },
    goMv() {
      this.$router.push({
        path: `/mv/${new Date().getTime()}`,
      })
    },
    goSinger() {
      this.$router.push({
        path: `/singer/${new Date().getTime()}`,
      })
    },
    goRank() {
      this.$router.push({
        path: `/rank/${new Date().getTime()}`,
      })
    },
    goTV() {
      this.$message({
        showClose: true,
        message:"未开发",
        type: "success"
      });
    },
    // 获取登录 key
    login() {
      let timerstamp = new Date().getTime();
      req_login_key(this, {
         timerstamp
      }).then(res => {
        this.unikey = res.data.unikey;
        // 获取二维码
        this.getQr();
        // 轮询登录状态
        this.getLogin_State();
      })
    },
    // 获取登录二维码
    getQr() {
      req_login_qr(this, {
        key: this.unikey,
        qrimg: true,
        timerstamp: new Date().getTime()
      }).then(res => {
        this.qrUrl = res.data.qrimg;
        this.dialogTableVisible = true;
      })
    },
    // 获取用户登录状态
    getLogin_State() {
       this.id = setInterval(()=>{
       req_login_state(this, {
           key: this.unikey,
           timerstamp: new Date().getTime()
       }).then(res => {
         if (res.code == 803) { // 登录成功
            clearInterval(this.id)
            this.dialogTableVisible = false;
            this.cookie = res.cookie;
         }
       })  
      }, 1000); 
    },
    // 获取用户信息
    getUserDetail() {
      req_user_detail(this).then(res => {
        this.user.userId = res.account.id;
        this.user.avatarUrl = res.profile.avatarUrl;
        this.user.nickname = res.profile.nickname;
   
        this.getUserPlayList();
      })
    },
    // 获取用户歌单
    getUserPlayList() {
      req_user_playList(this, {
         uid: this.user.userId
      }).then(res => {
        this.user.user_PlayList = res.playlist;
        this.loginFlag = true;
        window.localStorage.setItem("user", JSON.stringify(this.user));
        window.localStorage.setItem("loginFlag", this.loginFlag);
      })
    },
    getPlayDetail(id) {
      this.$router.push({
        path: `/playListDetail/${new Date().getTime()}`,
        query: {
          id,
        }
      })
    }
  },
};
</script>
<style lang="scss">
@import "../assets/scss/music.scss";
@import "../assets/scss/config.scss";
.sticky {
  position: sticky;
  top: 0;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.3);
  z-index: 100;
}
#header {
  position: relative;
  background: $colorD;
  transition: top 2s;
  .login {
    position: absolute;
    right: 160px;
    top: 20px;
    @include font(16px, 20px, #000);
    &:hover {
      color: $colorA;
      cursor: pointer;
    }
  }
  .user {
     @include flex(flex-start);
     position: absolute;
     right: 160px;
     .nick-name {
         margin-left: 6px;
         width: 160px;
         @include fontHidden();
         cursor: pointer;
     }
     img {
       display: block;
       width: 40px;
       height: 40px;
       border-radius: 50%;
     }
     .detail {
        display: none;
        position: absolute;
        top: 47px;
        left: -29px;
        z-index: 90;
      .detail-container {
          width: 180px;
          margin-left: 6px;
          border: 2px solid $colorA;
          padding: 3px;
          margin-top: 8px; 
          span {
            display:block;
            font-size: 12px;
            color: $colorA;
            @include fontHidden();
            padding: 10px 3px;
            box-sizing: border-box;
            &:hover {
               background-color: $colorA;
               color: #fff;
               cursor: pointer;
            }
          }
      }
      &:before {
        content: "";
        position: absolute;
        top: -21px;
        left: 34px;
        width: 0px;
        height: 0px;
        border: 15px solid transparent;
        border-bottom-color: $colorA; 
      
        display: block;
      }
      &:after {
        content: " ";
        display: block;
        position: absolute;
        top: -15px;
        left: 37px;
        width: 0px;
        height: 0px;
        border: 12px solid transparent;
        border-bottom-color: #fff; 
      }
     }
     &:hover {
       .detail {
         display: block;
       }
     }
  }
  .music-container {
    padding: 10px;
    .header-container {
      height: 40px;
      width: 1020px;
      @include flex();
      .item {
        text-align: left;
        flex-grow: 1;
        @include font(16px, 20px, $colorB);
        cursor: pointer;
        &:hover {
          color: $colorA;
        }
      }
    }
  }
  .el-dialog {
    width: 230px;
    .el-dialog__body {
      img {
        display: block;
        margin: 0 auto;
        width: 160px;
        height: 160px;
      }
    }
  }
}
</style>

