/**
 * 路由配置
 */

import Vue from 'vue'
import Router from 'vue-router'
import Home from './pages/Home.vue'


Vue.use(Router)
let router = new Router({
    routes: [
        {
            path: '/',
            name: 'home',
            component: Home,
            redirect: '/index',
            children: [
                {
                    // 主页面  /index
                    path: '/index',
                    name: 'Index',
                    component: () => import('./pages/Index.vue')
                },{
                    // MV 页面
                    path: '/mvdetail/:time',
                    name: 'MVDetail',
                    component: () => import('./pages/MvDetail.vue')
                },{
                    // playlist 页面
                    path: '/playListDetail/:time',
                    name: 'PlayListDetail',
                    component: () => import('./pages/PlayListDetail.vue')
                }, {
                    path: '/mv/:time',
                    name: 'Mv',
                    component: () => import('./pages/Mv.vue')
                }, {
                    path: '/singer/:time',
                    name: 'Singer',
                    component: () => import('./pages/Singer.vue')
                }, {
                    path: '/rank/:time',
                    name: 'Rank',
                    component: () => import('./pages/Ranking.vue')
                }, {
                    path: '/playList/:time',
                    name: 'PlayList',
                    component: () => import('./pages/PlayList.vue')
                }, {
                    path: '/singerDetail/:time',
                    name: 'SingerDetail',
                    component: () => import('./pages/SingerDetail.vue')
                }
            ]
        },{
            path: '/song/:time',
            name: 'Song',
            component: () => import('./pages/Song.vue')
        }
    ]
})

export default router