<template>
  <div id="app" :key="$route.fullPath">
    <router-view></router-view>
    <loading :flag="isloading"></loading>
  </div>
</template>

<script>
import loading from "./components/loading";

export default {
  name: "App",
  components: {
    loading,
  },
  data() {
    return {
      isloading: this.$store.state.loading,
    };
  },
  mounted() {
    
  },
  methods: {
    
  },
  watch: {
     // 监听 vuex 中 loading 的变化， 使其能够动态赋值给 data 数据中
    '$store.state.loading': function() {
       this.isloading = this.$store.state.loading;
    },
  },
};
</script>

<style lang="scss">
@import "./assets/scss/reset.scss";
@import "./assets/scss/base.scss";
html, body { 
  scroll-behavior:smooth; 
  }
#app {
  min-width: max-content;
}
</style>
