import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './actions'
Vue.use(Vuex)

const  state = {
    loading: true,
    // 是否显示音乐进度条
    musicPrograss: false,
    // 是否重新加载音乐
    musicLoading: "",
    // 是否播放
    play: false,
}
export default new Vuex.Store({
    state,
    mutations,
    actions
})