import { require } from "../require"
import { login_state, qr_create, login_key, user_detail, user_playList } from "../url"

/**
 * 获取登录 key
 */
function req_login_key(context, params = {}) {
    return require(login_key, params, context);
}

/**
 * 获取登录二维码
 */
function req_login_qr(context, params = {}) {
    return require(qr_create, params, context);
}

/**
 * 获取登录状态
 */
function req_login_state(context, params = {}) {
    return require(login_state, params, context);
}
/**
 * 获取账号信息
 */
function req_user_detail(context, params = {}) {
    return require(user_detail, params, context);
}
/**
 * 获取用户歌单
 */
function req_user_playList(context, params = {}) {
    return require(user_playList, params, context);
}
export {
    req_login_key,
    req_login_qr,
    req_login_state,
    req_user_detail,
    req_user_playList
}