export default {
    saveLoadingFlag(state, flag) {
        state.loading = flag;
    },
    saveMusicPrograss(state, flag) {
        state.musicPrograss = flag;
    },
    saveMusicLoading(state, num) {
        state.musicLoading = num;
    },
    savePlay(state, flag) {
        state.play = flag;
    },
}