<template>
  <div>
    <nav-header :flag="isSticky" :userFlag="userFlag"></nav-header>
    <router-view></router-view>
  </div>
</template>
<script>
import navHeader from "../components/navHeader";

export default {
  name: "Home",
  components: {
    navHeader,
  },
  data() {
    return {
      isSticky: false,
      userFlag: 0,
    };
  },
  watch: {
    '$route':{
      handler() {
        this.userFlag =  Math.random();
      },
      immediate: true
    }
  },
 
  mounted() {
    window.addEventListener("scroll", this.menu);
  },
  methods: {
    menu() {
      let value = document.documentElement.scrollTop || document.body.scrollTop;
      this.isSticky = value > 175 ? true : false;
    },
  },
};
</script>
