import Vue from 'vue'
import App from './App.vue'
import axios from 'axios'
// VueX
import store from './store/index'
// 图片懒加载
import VueLazyLoad from 'vue-lazyload'

// Element UI
import { Pagination } from 'element-ui';
import { Message } from 'element-ui'
import { Dialog } from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

// 路由
import router from './router'

Vue.use(Pagination);
Vue.use(Dialog);
Vue.prototype.$message = Message

Vue.config.productionTip = false

// axios 挂载 vue 原型上 后期直接使用 this.axios 直接使用即可
Vue.prototype.axios = axios

// 部署到服务器（自己）
// axios.defaults.baseURL = '/api';
// axios.defaults.baseURL = 'http://39.104.61.32:3000/'

// 设置允许携带 cookie
axios.defaults.withCredentials = true;
// 部署到第三方服务器
axios.defaults.baseURL = 'https://cloud-music-api-sandy.vercel.app/'

// 设置超时
// axios.defaults.timeout = 10000;


// 请求拦截器
axios.interceptors.request.use(function (config) {
  if(config.url != "/login/qr/check") {
    store.dispatch("saveLoadingFlag", true);
  }
 
  // 必须返回 config 不然会报错
  return config;
}, function (error) {
  return Promise.reject(error);
})
// 响应拦截器
axios.interceptors.response.use(function (response) {
  let status = response.status;
  if (status === 200) {
    store.dispatch("saveLoadingFlag", false);
    return Promise.resolve(response.data);
  } else {
    return Promise.reject(response.status);
  }
})

//图片懒加载
Vue.use(VueLazyLoad, {
  loading: '/img/loading-svg/loading-bars.svg'
})

new Vue({
  store,
  render: h => h(App),
  // 路由注册
  router,
}).$mount('#app')
