export default {
    // 保存加载标记
    saveLoadingFlag(context, flag) {
        context.commit('saveLoadingFlag', flag);
    },
    // 保存音乐进度条标记
    saveMusicPrograss(context, flag) {
        context.commit('saveMusicPrograss', flag);
    },
     // 保存音乐是否加载
    saveMusicLoading(context, num) {
        context.commit('saveMusicLoading', num);
    },
    savePlay(context, flag) {
        context.commit('savePlay', flag);
    },
}